label{
    color: #fff;
}
.form{
    min-height: 30vh;
    width:90%;
    margin: 0 auto;
    padding: 1rem;

}
form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.input{
  padding:0.3rem 2rem ;
  border-radius: 2rem;
  
}
textarea{
min-width: 13.5rem;
min-height: 8rem;
resize: none;
border-radius: 1rem;
padding: 0.5rem;
}
textarea::placeholder{
    text-align: center;
    line-height: 5rem;
}
.button{
padding:0.5rem 1.5rem;
border-radius: 10px;
border: none;
margin-top: 1rem;
cursor: pointer;
}
.button:hover{
    background-color:orange;
    color: #fff;
    transition: 1s;
}
@media screen and (max-width:480px) {
    .input{
        padding:0.3rem 1.5rem ;
      }
      textarea{
      min-width: 11.5rem;
     
      }
    
}