.heroimg{
   height:60vh;
   width: 100%;
   background: rgba(0, 0, 0, 0.5);
   position: relative;
   
}
.heading{
    display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   width: 100%;
   height: 100%;
}
.heroimg::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1537432376769-00f5c2f4c8d2?q=80&w=1450&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.heroimg h1{
    font-size: 2.4rem;
}
.heroimg p{
    font-size: 1.4rem;
    text-align: center;
}
@media screen and (max-width: 640px) {
   .heroimg h1{
    font-size: 2rem;
}
.heroimg p{
    font-size: 1.2rem;
}
}